<template>
  <div class="container">
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="50px" class="demo-ruleForm" label-suffix=":" label-position="left">
      <el-form-item label="账号" prop="userName">
        <el-input type="text" v-model="ruleForm.userName" autocomplete="off" placeholder="请输入用户名"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input type="password" v-model="ruleForm.password" autocomplete="off" placeholder="请输入密码"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button class="login-btn" type="primary" @click="submitForm('ruleForm')" size="small">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data () {
    var validateUserName = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入用户名'))
      } else {
        callback()
      }
    }
    var validatePassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        callback()
      }
    }
    return {
      ruleForm: {
        userName: '',
        password: ''
      },
      rules: {
        userName: [
          { validator: validateUserName, trigger: 'blur' }
        ],
        password: [
          { validator: validatePassword, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) return false
        let params = {
          name: this.ruleForm.userName,
          password: this.ruleForm.password
        }
        this.$http.login(params).then((res) => {
          if (!res) return
          this.$store.commit('setLogin', true)
          this.$store.commit('setAdmin', { id: res.data.result_rows.admin_id, name: res.data.result_rows.name })
          this.$router.push({ path: '/' })
        })
      })
    }
  }
}
</script>
<style lang="less" scoped>
  .container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
  }
  .login-btn {
    width: 118px;
    margin: 0 auto;
    display: block;
  }
</style>
